import { NgModule, SkipSelf, Optional } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HeaderComponent } from "./header/header.component";
import { FooterComponent } from './footer/footer.component';
import { SidebarComponent } from './sidebar/sidebar.component';
import { SharedModule } from "../shared/shared.module";
import { HttpModule } from "@angular/http";
import { HttpClientModule } from "@angular/common/http";
import { MatIconRegistry } from "@angular/material";
import { DomSanitizer } from "@angular/platform-browser";
import { loadSvgResources } from "../tool/svg.tool";
import { SidebarRoutingModule } from "./core.routing.module";

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    HttpModule,
    HttpClientModule,
    SidebarRoutingModule,
  ],
  declarations: [
    HeaderComponent, 
    FooterComponent, 
    SidebarComponent,
  ],
  exports: [
    HeaderComponent, 
    FooterComponent, 
    SidebarComponent, 
  ]
})
export class CoreModule { 
  constructor (@Optional() @SkipSelf() parent: CoreModule, ir:MatIconRegistry, ds:DomSanitizer) {
    if (parent) {
      throw new Error('module is already exist,cannot be loaded again');
    }
    loadSvgResources(ir, ds);
  }
}



