import { Component, OnInit } from '@angular/core';
import { MatDialog } from "@angular/material";
import { NewProjectComponent } from "../new-project/new-project.component";
import { InviteSomeoneComponent } from "../invite-someone/invite-someone.component";

@Component({
  selector: 'app-project-list',
  templateUrl: './project-list.component.html',
  styleUrls: ['./project-list.component.scss'],
})
export class ProjectListComponent implements OnInit {
  
  project: object[] = [
    {
      "name": "企业协作平台",
      "desc": "这是一个企业内部项目",
      "coverImg": "../../../assets/covers/0.jpg",
    },
    {
      "name": "企业协作平台",
      "desc": "这是一个企业内部项目",
      "coverImg": "../../../assets/covers/0.jpg",
    },
  ]
  constructor(private dialog: MatDialog) { }

  ngOnInit() {
  }
  openNewProjectDialog() {
    const openDialog = this.dialog.open(NewProjectComponent, {position: {top: '160px'}, data: {dark: true, msg: 'send a message to dialogComponent'}});
    openDialog.afterClosed().subscribe( getMsg => console.log(getMsg) )
  }
  launchInviteDailog() {
    const openDialog = this.dialog.open(InviteSomeoneComponent);
    openDialog.afterClosed().subscribe( getMsg => console.log(getMsg) )
  }
}



